<template>
  <el-form :model="parmes" ref="ruleForm" label-width="100px" class="demo-ruleForm">
    <el-form-item label="">
      <el-radio-group v-model="parmes.source">
        <el-radio label="product"></el-radio>
        <el-radio label="group"></el-radio>
      </el-radio-group>
    </el-form-item>
    <el-form-item label="">
      <el-radio-group v-model="parmes.style">
        <el-radio label="1"></el-radio>
        <el-radio label="2"></el-radio>
      </el-radio-group>
    </el-form-item>
  </el-form>
</template>

<script>
  export default {
    name: 'productlist',
    props: ['parmes']
  }
</script>

<style scoped>

</style>



