<template>
  <ul class="product-list clearfix">
    <li>
      <a href="##">
        <div class="pic">
          <img src="./pic_220_1.jpg"/>
        </div>
        <h3 class="name">   120g</h3>
        <span class="price"><span class="make"></span>17.00</span>
      </a>
    </li>
    <li>
      <a href="##">
        <div class="pic">
          <img src="./pic_220_1.jpg"/>
        </div>
        <h3 class="name">Colgate   4  </h3>
        <span class="price"><span class="make"></span>9.90</span>
        <del class="del"><span class="make"></span>17.00</del>
      </a>
    </li>
    <li>
      <a href="##">
        <div class="pic">
          <img src="./pic_220_1.jpg"/>
        </div>
        <h3 class="name">  5L </h3>
        <span class="price"><span class="make"></span>49.00</span>
      </a>
    </li>
    <li>
      <a href="##">
        <div class="pic">
          <img src="./pic_220_1.jpg"/>
        </div>
        <h3 class="name">Pampers   S1644-8kg</h3>
        <span class="price"><span class="make"></span>149.00</span>
        <del class="del"><span class="make"></span>158.00</del>
      </a>
    </li>
  </ul>
</template>

<script>
    export default {
        name: 'productlist'
    }
</script>

<style scoped>
  .product-list {
    background: #fff;
    padding-bottom: .5rem;
  }
  .product-list li {
    float: left;
    width: 47%;
    margin-left: 2%;
    margin-top: .5rem;
  }
  .product-list li a {
    display: block;
    position: relative;
  }
  .product-list li .label {
    position: absolute;
    left: 0;
    top: 0;
  }
  .product-list li .pic {
    position: relative;
    width: 100%;
    height: 0;
    padding-bottom: 100%;
    overflow: hidden;
    margin-bottom: .3rem;
  }
  .product-list li .pic img {
    position: absolute;
    width: 100%;
    height: 100%;
  }
  .product-list li .name {
    line-height: .8rem;
    height: 1.6rem;
    color: #666;
    overflow: hidden;
    margin-bottom: .3rem;
  }
  .product-list li .price {
    font-size: .6rem;
    font-weight: 700;
    margin-right: .2rem;
  }
  .product-list li .price .make {
    font-size: .5rem;
  }
  .product-more {
    display: block;
    text-align: center;
    color: #999;
    border-top: 1px solid #eee;
    line-height: 1.5rem;
    background: #fff;
  }
  .product-more s {
    display: inline-block;
    vertical-align: top;
    margin: .5rem 0 0 .2rem;
    width: .45rem;
    height: .45rem;
    background: url(arrow_right.png) no-repeat;
    background-size: auto 100%;
    opacity: .45;
  }
</style>



